<template>
  <Page title="Gallery" desc="画廊，可实现上传图片的展示或幻灯片播放">
    <weui-button @click="show = true">查看图片</weui-button>
    <weui-gallery
      :visible="show"
      :src="'data:image/png;base64,'"
      :canDelete="true"
      @onClick="hide()"
      @onDelete="deleteGallery()"
    ></weui-gallery>
  </Page>
</template>
<script>
import Page from '@/components/Page.vue'

export default {
  data () {
    return {
      show: false
    }
  },
  components: { Page },
  methods: {
    hide: function () {
      this.show = false
    },
    deleteGallery: function () {
      console.log('删除成功')
    }
  }
}
</script>